@use "sass:map";
@use  "sass:color";
.footer {
  padding: 6em 0 8em;
  font-family: $openSans-font;
  @include tablet {
    padding: 5.5em 0 7.4em;
  }
  @include mobile {
    padding: 2.5em 0 1.875em;
  }
  .row {
    @include mobile {
      display: flex;
      flex-direction: column;
    }
    .col-3 {
    padding: 2.5em 0 0 2.5em;
    @include tablet {
      padding: 1.5625em 0 0 1.5625em;
    }
    @include mobile {
      padding: 1.25em 0 0 1.25em;
      width: 100%;
    }
  }
  }
}

.widget-links {
  padding-bottom: 2em;
  @include mobile {
    &::after {
      content: "";
      border-bottom: 1px solid map.get($colors, bottom-footer);
      padding-right: 100%;
      padding-bottom: 1em;
    }
  }
  h3 {
    @extend %h3-style;
    margin-bottom: 1em;
    @include tablet {
      font-size: 1.25em;
    }
    @include mobile {
      font-size: 1.0625em;
    }
  }
  p {
    font-size: 1em;
    line-height: 2em;
    color: map.get($colors, navigation-text);
    font-weight: 300;
    @include tablet {
      font-size: 0.9em;
    }
    @include mobile {
      font-size: 0.9em;
    }
  }
}
.list-links li a {
  @extend %text-style;
  @include tablet {
    font-size: 0.9em;
  }
  @include mobile {
    font-size: 0.9em;
  }
}
.contact-links{
  display: flex;
  margin-bottom: 1.5em;
  li {
    width: 2.8125em;
    height: 2.8125em;
    background-color: map.get($colors, navigation-text);
    margin-right: 0.25em;
    border-radius: 0.375em;
    &:hover {
      background-color: lighten(#696969,5%);
      cursor: pointer;
      transition: 0.5s;
    }
    @include tablet {
      width: 2.25em;
      height: 2.25em;
    }
    .icon-brands {
      font-size: 0.9em;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      color: map.get($colors, box-background);
      @include tablet {
        font-size: 0.75em;
      }
      @include mobile {
        font-size: 0.75em;
      }
    }
  }
}
.menu-bottom {
  display: flex;
  justify-content: center;
  margin-bottom: 2em;
  color: map.get($colors, bottom-footer);
  @include mobile {
    display: flex;
    flex-direction: column;
  }
  li {
    font-size: 1em;
    line-height: 2em;
    font-weight: 300;
    @include tablet {
      font-size: 0.9em;
    }
    @include mobile {
      font-size: 0.9em;
    }
    a {
      color: map.get($colors, bottom-footer);
      text-decoration: underline;
      &:hover {
        text-decoration: none;
        cursor: pointer;
      }
    }
    &:first-child, &:nth-child(2) {
      &::after {
        content: "";
        border-left: 0.0625em solid map.get($colors, bottom-footer);
        margin-left: 0.625em;
        padding: 0.3125em 0.625em 0.3125em 0;
        @include tablet {
          padding: 0.285em 0.5em 0.285em 0;
        }
        @include mobile {
          display: none;
        }
      }
    }
  }
}